.wp-block-button__link {
  background-color: var(--bleu-bouton);
  text-transform: uppercase; }

a.wp-block-button__link {
  border: none;
  cursor: pointer;
  display: inline-block;
  padding: 1rem 1.2rem 0.8rem;
  text-align: center;
  vertical-align: middle;
  background: var(--bleu-bouton);
  border-radius: 1rem;
  color: var(--blanc);
  font-size: 1em;
  font-weight: 400;
  letter-spacing: 1.05px;
  line-height: 1.1;
  min-height: 4.4rem;
  min-width: 21rem;
  text-decoration: none;
  text-transform: uppercase;
  transition: all var(--transition); }
  a.wp-block-button__link:hover, a.wp-block-button__link:focus, a.wp-block-button__link:active {
    background: var(--bleu-fonce);
    outline: none;
    text-decoration: none; }
  a.wp-block-button__link:focus-visible {
    outline: solid 1px var(--bleu-fonce); }

.block-list-appender button, button.acf-button, .wp-editor-tabs button {
  min-width: unset;
  padding: unset; }
  .block-list-appender button, .block-list-appender button:hover, .block-list-appender button:focus, button.acf-button, button.acf-button:hover, button.acf-button:focus, .wp-editor-tabs button, .wp-editor-tabs button:hover, .wp-editor-tabs button:focus {
    background-color: transparent; }

.wp-block-columns.antennes-regionales {
  align-items: center; }
  .wp-block-columns.antennes-regionales ol {
    color: var(--bleu);
    font-weight: 700; }
    .wp-block-columns.antennes-regionales ol li {
      margin-bottom: 1em; }

.decor-traits {
  position: relative; }
  .decor-traits::before {
    background-image: url("icons/decor-droit.svg");
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: contain;
    content: '';
    position: absolute;
    height: 600px;
    right: -300px;
    width: 545px;
    top: -200px; }
    @media (min-width: 768px) {
      .decor-traits::before {
        top: -120%;
        height: 872px;
        right: -90%;
        width: 823px; } }

.wp-block-image figcaption {
  font-style: italic; }

.wp-block-image.alignwide {
